body {
  font-family: CeraPro;
  overflow-x: hidden;
}
.color-white {
  color: #fff;
}
a {
  outline: none;
}
a:hover {
  color: #fff;
  text-decoration: none;
}
.big-title {
  color: #000;
  font-size: 60px;
  font-weight: 700;
  line-height: 36px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .big-title {
    font-size: 36px;
  }
}
.middle-title {
  color: #000;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
}
@media (min-width: 320px) and (max-width: 991px) {
  .middle-title {
    font-size: 24px;
  }
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
.modal.fade.show {
  padding-right: 0 !important;
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Thin.woff") format('woff');
  font-weight: 100;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Light.woff") format('woff');
  font-weight: 300;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Medium.woff") format('woff');
  font-weight: 400;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Regular.woff") format('woff');
  font-weight: 500;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Bold.woff") format('woff');
  font-weight: 700;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Black.woff") format('woff');
  font-weight: 800;
  font-style: 'normal';
}
.header-archive {
  width: 100%;
  background-color: #383838;
}
.header-archive .logo-block {
  padding: 46px 0;
}
.header-archive .logo {
  width: 266px;
  height: 132px;
  margin: 0 auto;
  background-size: cover;
  background-position: center;
}
.archive-tab {
  padding-top: 90px;
}
.archive-tab .titles-block {
  text-align: center;
}
.archive-tab .titles-block .return-home {
  margin-bottom: 12px;
  display: block;
  color: #16b1a1;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 1.29px;
  text-transform: uppercase;
  line-height: 1;
}
.archive-tab .titles-block .title {
  color: #000;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
}
.archive-tab .nav-block {
  margin-top: 65px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.archive-tab .nav-block .nav-tabs {
  border-bottom: none;
  margin-bottom: 68px;
}
.archive-tab .nav-block .nav-item {
  color: #1dc5b4;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
}
.archive-tab .nav-block .nav-item.active {
  color: #fff;
  background-color: #1dc5b4;
}
.archive-tab .nav-block .nav-link {
  border: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  padding: 14px 35px;
}
.archive-tab .tab-content .item {
  margin-bottom: 26px;
}
.archive-tab .tab-content .date-block {
  display: inline-block;
  width: 10%;
}
@media (min-width: 768px) and (max-width: 991px) {
  .archive-tab .tab-content .date-block {
    width: 18%;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .archive-tab .tab-content .date-block {
    width: 100%;
  }
}
@media (max-width: 575px) {
  .archive-tab .tab-content .date-block {
    width: 100%;
  }
}
.archive-tab .tab-content .date {
  display: inline-block;
  vertical-align: middle;
  padding-top: 16px;
  width: 76px;
  height: 95px;
  background-color: #1dc5b4;
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
}
.archive-tab .tab-content .date p {
  font-size: 11px;
  text-transform: uppercase;
}
.archive-tab .tab-content .text-block {
  display: inline-block;
  padding-left: 25px;
  width: 85%;
}
@media (min-width: 768px) and (max-width: 991px) {
  .archive-tab .tab-content .text-block {
    padding-left: 0;
    width: 80%;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .archive-tab .tab-content .text-block {
    padding-left: 0;
    width: 80%;
  }
}
@media (max-width: 575px) {
  .archive-tab .tab-content .text-block {
    padding-left: 0;
    width: 100%;
  }
}
.archive-tab .tab-content .text-block .title {
  color: #000;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 22px;
}
.archive-tab .pagination {
  justify-content: center;
  padding-top: 26px;
  padding-bottom: 70px;
}
@media (min-width: 320px) and (max-width: 991px) {
  .archive-tab .pagination {
    padding-bottom: 35px;
  }
}
.archive-tab .pagination li.prev:before {
  padding: 0.5rem 0.75rem;
  content: url("/public/img/icons/prev-pagination.png");
  display: block;
}
.archive-tab .pagination li.next a:after {
  content: url("/public/img/icons/next-pagination.png");
  display: block;
}
.archive-tab .pagination li a {
  position: relative;
  display: block;
  padding: 0.5rem 0.75rem;
  color: #a3a3a3;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 14.31px;
  text-transform: uppercase;
  border: none;
}
.archive-tab .pagination li a.active {
  border: 1px solid #1dc5b4;
  border-radius: 50%;
  color: #000;
  margin: 0 2px;
}
